<template>
    <div class="registro">
        <img src="../assets/LOGO1.png" alt="logo churrasco">
        <h1>Alta de usuario</h1>
        <form @submit.prevent="registrarUsuario(usuario)">
            <input type="name" placeholder="Ingrese su nombre de usuario" v-model="usuario.name"><br>
            <input type="email" placeholder="Ingrese su email" v-model="usuario.email"><br>
            <input type="password" placeholder="Ingrese su contraseña" v-model="usuario.password"><br>
            <button type="submit">Registrarse</button>
        </form>
    
    </div>
</template>

<script>

export default {
    data(){
        return{
            usuario:{
                name:'',
                email: '',
                password:''
            }
        }
    },
    methods:{
    async registrarUsuario(nuevoUsuario){
        try {
            const res = await fetch('https://jwt-gabriel-api.herokuapp.com/api/usuario/registro', {
            method:'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(nuevoUsuario)
        })
        const resDB= await res.json()
        console.log(resDB)
        window.alert('Bienvenido a la pagina')
      } catch (error) {
        console.log(error)
      }
    }
    }

}
</script>
